@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@200;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

p {
    letter-spacing: 5px;
    font-family: 'Gemunu Libre', sans-serif;
    color: #7579E7;
}

h1, h3, p {
    text-align: center;
}

h1 {
    font-family: 'Gemunu Libre', sans-serif;
    font-weight: 700;
    letter-spacing: 2px;
}

h3 {
    font-family: 'Gemunu Libre', sans-serif;
    font-weight: 500;
    letter-spacing: 2px;
}
.about_box {
    background: #fff;
    border-radius: 15px;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.7);
    padding: 10px;
}

.pro_pic {
    height: 300px;
}

.edit_pro_btn {
    background: #0877F3 !important;
    color: #fff !important;
    font-size: 10px !important;
    transition: all 1s !important;
}

.edit_pro_btn:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.use_info {
    text-align: left;
    font-weight: normal;
    letter-spacing: normal;
    color: #000;
}
.contact_box {
    width: 100%;
    background: transparent;
    padding: 10px 10px;
    border-radius: 5px;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.9);
    height: 150px;
    transition: all 1s!important;
  }
  
  .contact_box h1 {
    text-align: left;
    color: #5C7AEA;
  }
  
  .contact_box:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .contact_form_box {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.9);
  }
  
  .contact_form_box h1 {
    text-align: left;
  }
  
  .contact_input {
    width: 100%;
  }
  
  .contact_msg {
    width: 100%;
    resize: both;
    overflow: hidden;
  }
  
  .msg_btn {
    margin-top: 10px!important;
    background: #47A5EF!important;
    padding: 10px!important;
    margin-left: 10px!important;
    color: #fff!important;
    transition: all 1s!important;
  }
  
  .msg_btn:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
background: #F1F6F9;
font-family: 'Roboto', sans-serif;
}

.signup_form {
  width: 75%;
  margin: auto;
  height: 80vh;
  display: flex;
  flex-direction: column;
  padding: 5px 10px;
  background: #fff;
  color: #000;
  border-radius: 15px;
  box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.9);
}

.input {
  width: 75%;
  font-size: 30px !important;
}

.signup_btn {
  background: #25D32E !important;
  color: #fff !important;
  margin-top: 5px!important;
  border-radius: 5px!important;
}

.signin_btn {
  background: #290FBA !important;
  color: #fff !important;
  margin-top: 5px!important;
  border-radius: 5px!important;
}

@media (max-width: 640px) {
  .signup_form {
    height: 100%;
    padding-bottom: 15px;
  }
  .contact_box {
    width: 98%;
    margin: auto;
    overflow: hidden;
  }
}

